.btn-episode-watched-group .btn {
  background-color: #414141;
  color: #E2E2E2;
  border: 1px solid #6C6C6C;
  border-radius: 0;
  outline: 0; }
  .btn-episode-watched-group .btn:hover, .btn-episode-watched-group .btn:active {
    background-color: #373737;
    outline: 0; }

.btn-episode-watched-group .btn:active {
  outline: 0; }

.btn-episode-watched-group .btn-times {
  cursor: default; }
  .btn-episode-watched-group .btn-times:hover, .btn-episode-watched-group .btn-times:active {
    background-color: #414141;
    outline: 0; }

.btn-episode-watched-group .dropdown-menu {
  top: -20px;
  right: -7px;
  min-width: 42px; }
  .btn-episode-watched-group .dropdown-menu li {
    text-align: center;
    font-size: 18px;
    cursor: pointer;
    padding: 2px; }

.btn-episode-watched-group .watched {
  background-color: #068223; }
  .btn-episode-watched-group .watched:hover {
    background-color: #008F21; }

.btn-episode-watched-group .watching {
  background: -webkit-gradient(linear, left top, right top, color-stop(56%, #428bca), color-stop(44%, #414141));
  background: linear-gradient(90deg, #428bca 56%, #414141 44%); }

.btn-episode-watched-group .watching-watched {
  background: -webkit-gradient(linear, left top, right top, color-stop(56%, #428bca), color-stop(44%, #068223));
  background: linear-gradient(90deg, #428bca 56%, #068223 44%); }

.btn-episode-watched-group .fa-plus {
  color: #1DC25B; }

.btn-episode-watched-group .fa-minus {
  color: #FF332F; }
