.episode-box-list-item {
  width: 100%;
  padding-left: 5px;
  padding-right: 5px;
  background-color: #414141;
  color: #E2E2E2;
  border-bottom: 1px solid #515151; }
  .episode-box-list-item .title {
    font-size: 16px;
    padding-top: 2px; }
  .episode-box-list-item .meta {
    font-size: 14px; }
  .episode-box-list-item .title, .episode-box-list-item .meta {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
  .episode-box-list-item .button-bar {
    margin-top: 5px;
    margin-bottom: 5px; }
  .episode-box-list-item .play-button {
    cursor: pointer;
    font-size: 28px; }
    .episode-box-list-item .play-button .fa {
      pointer-events: none; }
